import React, { useState, useEffect } from "react";
import {
  Button,
  CircularProgress,
  Container,
  Paper,
  Typography,
} from "@mui/material";
import { Link } from "react-router-dom";

interface Data {
  city: string;
  dailyForecast: DailyWeather[];
  virhe: string;
  valmis: boolean;
}

interface EnnusteProps {
  selectedCity: string;
}

const Ennuste: React.FC<EnnusteProps> = ({
  selectedCity,
}): React.ReactElement => {
  const [data, setData] = useState<Data>({
    city: "",
    dailyForecast: [],
    virhe: "",
    valmis: false,
  });

  const [loading, setLoading] = useState(false);

  const formatDate = (timestamp: number): string => {
    const date = new Date(timestamp * 1000);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();

    return `${day}.${month}.${year}`;
  };

  const fetchData = async (): Promise<void> => {
    try {
      setLoading(true);
      const normalizedCity = selectedCity.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
      const response = await fetch(
        `https://xamkbit.azurewebsites.net/saaennuste/${normalizedCity}`
      );
      const result = await response.json();
     // console.log("API Response:", result);

      const currentDate = new Date();
      const currentDateString = formatDate(currentDate.getTime() / 1000);

      const dailyForecast = result.list.reduce(
        (acc: DailyWeather[], forecast: any) => {
          const date = formatDate(forecast.dt);

          const time = forecast.dt_txt.split(" ")[1];

          if (
            date !== currentDateString &&
            (time === "03:00:00" || time === "15:00:00") &&
            forecast.weather[0]
          ) {
            const existingDayIndex = acc.findIndex((day) => day.date === date);

            if (existingDayIndex === -1) {
              acc.push({
                id: forecast.dt,
                date,
                temperatureNight:
                  time === "03:00:00" ? forecast.main.temp.toString() : "",
                weatherNight: {
                  id: forecast.weather[0]?.id || 0,
                  main: forecast.weather[0]?.main || "",
                  description: forecast.weather[0]?.description || "",
                  icon: forecast.weather[0]?.icon || "",
                },
                temperatureDay:
                  time === "15:00:00" ? forecast.main.temp.toString() : "",
                weatherDay: {
                  id: forecast.weather[0]?.id || 0,
                  main: forecast.weather[0]?.main || "",
                  description: forecast.weather[0]?.description || "",
                  icon: forecast.weather[0]?.icon || "",
                },
              });
            } else {
              if (time === "03:00:00") {
                acc[existingDayIndex].temperatureNight =
                  forecast.main.temp.toString();
                acc[existingDayIndex].weatherNight = {
                  id: forecast.weather[0]?.id || 0,
                  main: forecast.weather[0]?.main || "",
                  description: forecast.weather[0]?.description || "",
                  icon: forecast.weather[0]?.icon || "",
                };
              } else {
                acc[existingDayIndex].temperatureDay =
                  forecast.main.temp.toString();
                acc[existingDayIndex].weatherDay = {
                  id: forecast.weather[0]?.id || 0,
                  main: forecast.weather[0]?.main || "",
                  description: forecast.weather[0]?.description || "",
                  icon: forecast.weather[0]?.icon || "",
                };
              }
            }
          }

          return acc;
        },
        []
      );

      setData({
        city: result.city?.name || "",
        dailyForecast,
        virhe: "",
        valmis: true,
      });
    } catch (error: any) {
      // console.error('Error fetching data:', error);
      setData({
        ...data,
        virhe: "Palvelimelle ei saatu yhteyttä.",
        valmis: true,
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, [selectedCity]);

  return (
    <Container sx={{ display: "flex" }}>
      <Paper
        sx={{ margin: 4, minWidth: "495px", minHeight: "100px", padding: 4 }}
      >
        <Typography variant="h6" style={{ marginBottom: "20px" }}>
          Sää Ennuste:
        </Typography>

        {loading ? (
          <CircularProgress />
        ) : data.valmis ? (
          <>
            <Typography>
              <b>Kaupunki</b>: {`${data.city}`}
            </Typography>
            {data.dailyForecast.map((day) => (
              <div
                key={day.id}
                style={{
                  marginBottom: "20px",
                  border: "1px solid #ccc",
                  padding: "10px",
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "start",
                }}
              >
                {day.weatherNight && (
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      alignItems: "start",
                      width: "100%",
                    }}
                  >
                    <img
                      src={`https://openweathermap.org/img/wn/${day.weatherNight.icon}@2x.png`}
                      alt="Weather Icon"
                      style={{ width: "50px", height: "50px" }}
                    />
                    <Typography>
                      <b>Yön sää:</b>
                      <br />
                      {`${day.weatherNight.description}`}
                    </Typography>
                    <Typography>
                      <b>Lämpötila:</b>
                      <br />
                      {`Lämpötila: ${day.temperatureNight} Celsius`}
                    </Typography>
                    <Typography>
                      <b>Aika:</b>
                      <br />
                      {`03:00`}
                    </Typography>
                  </div>
                )}
                {day.weatherDay && (
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      alignItems: "start",
                      width: "100%",
                    }}
                  >
                    <img
                      src={`https://openweathermap.org/img/wn/${day.weatherDay.icon}@2x.png`}
                      alt="Weather Icon"
                      style={{ width: "50px", height: "50px" }}
                    />
                    <Typography>
                      <b>Iltapäivän sää:</b>
                      <br />
                      {`${day.weatherDay.description}`}
                    </Typography>
                    <Typography>
                      <b>Lämpötila:</b>
                      <br />
                      {`${day.temperatureDay} Celsius`}
                    </Typography>
                    <Typography>
                      <b>Aika:</b>
                      <br />
                      {`15:00`}
                    </Typography>
                    <Typography>
                      <b>Päivä</b>
                      <br />
                      {`${day.date}`}
                    </Typography>
                  </div>
                )}
              </div>
            ))}
            <Button component={Link} to="/">
              Siirry Etusivulle
            </Button>
          </>
        ) : (
          <Typography variant="body2">{data.virhe}</Typography>
        )}
      </Paper>
    </Container>
  );
};

export default Ennuste;