import { Button, Container, Grid, Paper, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import TehtavaLista from './TehtavaLista' 
import PaivanSaa from "./PaivanSaa";
import Ennuste from "./Ennuste";


interface EtusivuProps {
  havainto: Havainto[];

}

const Etusivu: React.FC<EtusivuProps> = ({ havainto }): React.ReactElement => {
    const [selectedCity, setSelectedCity] = useState<string>("");
  useEffect(() => {
    // console.log("(Etusivu.tsx) - Päivitetty havainto state:(Etusivu.tsx)",havainto);
  }, [havainto]);

  return (
    <Container sx={{ display: "flex" }}>
      <Grid container spacing={2}>

        <Grid item xs={6}>
          <Paper
            sx={{
              margin: 4,
              minWidth: "495px",
              minHeight: "100px",
              padding: 4,
            }}
          >
            <Typography variant="body1" style={{ marginBottom: "20px" }}>
              Alapuolella olevasta linkistä, pääset lisäämään tehtäviä ja paikan missä tehtävä pitää suorittaa.
             Lisätyt tehtävät tulevat näkyviin, ja voit tarvittaessa muokata, tai poistaa niitä.
            </Typography>
            <Button component={Link} to="/lisaatehtava">
              Lisää uusi tehtävä
            </Button>
          </Paper>
        </Grid>

        <Grid item xs={6}>
           <PaivanSaa selectedCity={selectedCity} onSelectCity={setSelectedCity}/>  
        </Grid>

        <Grid item xs={6}>
          <Paper
            sx={{
              margin: 4,
              minWidth: "495px",
              minHeight: "100px",
              padding: 4,
            }}
          >
            <TehtavaLista havainto={havainto} />
          </Paper>
        </Grid>

        <Grid item xs={6}>
            <Ennuste selectedCity={selectedCity} />
        </Grid>
      </Grid>
    </Container>
  );
};

export default Etusivu;
