import { Button, Container, Dialog, Grid, List, ListItem, ListItemText, Paper, TextField, Typography } from '@mui/material';
import { DateTimePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from "date-fns/locale";
import React, { useEffect, useRef, useState } from 'react';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';


interface MuokkaaProps {
  havainto: Havainto[];
  setHavainto: (arg0: Havainto[]) => void;

}

const MuokkaaTehtava: React.FC<MuokkaaProps> = ({ havainto, setHavainto }) => {

  const [editedHavainto, setEditedHavainto] = useState<Havainto | null>(null);

  const navigate: NavigateFunction = useNavigate();
  const { id } = useParams<any>();

  // Etsi valittu Havainto editotavaksi
  const editHavainto: Havainto | undefined = havainto.find((havainnot: Havainto) => havainnot.id === id);




  useEffect(() => {
    // Päivitä editedHavainto tila kun editHavainto propsi muuttuu
    setEditedHavainto(editHavainto || null);
  }, [editHavainto]);

  // Hanskaa muutokset editedHavainto tilassa
  const handleChange = (field: keyof Havainto, value: string) => {
    setEditedHavainto((prev) => ({
      ...prev!,
      [field]: value,
    }));
  };

  // Hanskaa editoidin tallennus
  const saveEdit = () => {
    if (editedHavainto && editedHavainto.pvm) {
      // Update the havainto array with the editedHavainto
      const updatedHavainnot = havainto.map((havainnot: Havainto) => havainnot.id === editedHavainto.id ? editedHavainto : havainnot
      );
      // Lisätään päivitetty listas tilaan
      setHavainto(updatedHavainnot);

      // Menee takas etusivulle automatic, ku painaa tallenna
      navigate('/');
    }
  };

  return (
    <LocalizationProvider
      dateAdapter={AdapterDateFns}
      adapterLocale={fi}
    >
      <Dialog open>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Typography
              variant="h6"
              sx={{
                marginBottom: '20px',
                textAlign: 'center',
              }}
            >
              Muokkaa havaintoa
            </Typography>
            {editedHavainto && (
              <>
                <DateTimePicker
                  label="Päivämäärä ja aika"
                  value={editedHavainto.pvm}
                  onChange={(uusiPvm: Date | null) =>
                    setEditedHavainto((prev) => ({
                      ...prev!,
                      pvm: uusiPvm || new Date(),
                    }))
                  }
                  sx={{
                    marginBottom: '10px',
                    width: '100%',
                  }}
                />
                <TextField
                  label="Paikka"
                  value={editedHavainto.paikka}
                  onChange={(e) =>
                    handleChange('paikka', e.target.value)
                  }
                  variant="outlined"
                  fullWidth={true}
                  sx={{
                    marginBottom: '10px',
                    width: '100%',
                  }}
                />
                <TextField
                  label="Laji"
                  value={editedHavainto.laji}
                  onChange={(e) =>
                    handleChange('laji', e.target.value)
                  }
                  variant="outlined"
                  fullWidth={true}
                  sx={{
                    marginBottom: '10px',
                    width: '100%', 
                  }}
                />
                <Button
                  variant="contained"
                  fullWidth={true}
                  onClick={saveEdit}
                  sx={{
                    width: '100%',
                  }}
                >
                  Tallenna
                </Button>
                <Button fullWidth={true} component={Link} to="/">
                  Peruuta
                </Button>
              </>
            )}
          </Grid>
        </Grid>
      </Dialog>
    </LocalizationProvider>
  );
};


export default MuokkaaTehtava;