import { Button, Container, Dialog, Typography } from "@mui/material";
import React from "react";
import {
  Link,
  NavigateFunction,
  useNavigate,
  useParams,
} from "react-router-dom";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { fi } from "date-fns/locale";
import { LocalizationProvider } from "@mui/x-date-pickers";
import { format } from "date-fns";

interface PoistaTehtavaProps {
  havainto: Havainto[];
  setHavainto: (arg0: Havainto[]) => void;
}

const PoistaTehtava: React.FC<PoistaTehtavaProps> = ({havainto,setHavainto,}): React.ReactElement => {


  const navigate: NavigateFunction = useNavigate();
  const { id } = useParams<any>();

  const poistaTehtava: Havainto | undefined = havainto.find((havainnot: Havainto) => {
      return havainnot.id === id;
    }
  );

  const confirmPoisto = (): void => {
    setHavainto([
      ...havainto.filter((havainnot: Havainto) => havainnot.id !== id),
    ]);
    navigate("/");
  };

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
    <Container style={{padding: 15}}>
    <Dialog open>
      <Typography variant="h6" style={{textAlign: 'center'}}>Poista tehtävä</Typography>
      <Typography variant="subtitle1" style={{padding: 15}}>
        Haluatko varmasti poistaa tehtävän ? <br /> <b>Laji:</b> {poistaTehtava!.laji}
        <br /> 
        <Typography>

        <b>Paikka:</b> {poistaTehtava!.paikka}
        <br /> 
        <b>pvm:</b> {poistaTehtava ? format(new Date(poistaTehtava.pvm), "d.M.y HH:mm"): ""}

        </Typography>

      </Typography>
      <Button variant="contained" fullWidth={true} onClick={confirmPoisto}>
        {" "}
        Poista tehtävä
      </Button>
      <Button fullWidth={true} component={Link} to="/">
        Peruuta
      </Button>
    </Dialog>
    </Container>
    </LocalizationProvider>
  );
};

export default PoistaTehtava;