import React, { useRef, useState } from "react";
import { Button, Container, Grid, Paper, TextField, Typography } from "@mui/material";
import { Link, NavigateFunction, useNavigate } from "react-router-dom";
import { DateTimePicker, LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { fi } from "date-fns/locale";
import { v4 as uuid } from "uuid";

interface TehtavaProps {
  havainto: Havainto[];
  setHavainto: (arg0: Havainto[]) => void;
}

const LisaaTehtava: React.FC<TehtavaProps> = ({
  havainto,
  setHavainto,
}): React.ReactElement => {
  const [pvmAika, setPvmAika] = useState<Date>(new Date());
  const navigate: NavigateFunction = useNavigate();

  const uusiTehtavaPaikkaRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<
    HTMLInputElement
  >();
  const uusiTehtavaRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<
    HTMLInputElement
  >();

  const lisaaUusiHavainto = (): void => {
    let apuHavainto: Havainto = {
      id: uuid(),
      pvm: pvmAika,
      paikka: uusiTehtavaPaikkaRef.current!.value || "Nimetön paikka tehtävä",
      laji: uusiTehtavaRef.current!.value || "Nimetön Tehtävä",
    };
    setHavainto([...havainto, apuHavainto]);
    navigate("/");
;
  };

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
      <Container>
        <Typography variant="h6" sx={{ marginTop: "10px" }}>
          Lisää Tehtävä
        </Typography>
        <Typography variant="body1" sx={{ marginTop: "10px", marginBottom: "20px" }}>
          Tässä pääset lisäämään uusia tehtäviä. <br />
          Kirjattuasia tehtävän, tai peruutat lisäyksen<br />
          siirryt automaattisesti etusivulle, jossa näet kaikki<br />
          lisämääsi tehtävät.
        </Typography>

        <Grid container spacing={2}>
          <Grid item xs={2}>
            <Paper elevation={1} sx={{ minWidth: "200px", marginLeft: "50px", borderRadius: 2 }}>
              <Typography variant="h6" sx={{ marginBottom: "20px", textAlign: "center" }}>
                {" "}
                Uusi Tehtävä
              </Typography>
              <DateTimePicker
                label="Päivämäärä"
                value={pvmAika}
                onChange={(uusiPvm: Date | null) => setPvmAika(uusiPvm!)}
                slots={(params: any) => <TextField {...params} fullWidth={true} />}
              />
              <TextField
                label="Paikka"
                inputRef={uusiTehtavaPaikkaRef}
                variant="outlined"
                fullWidth={true}
                sx={{ marginBottom: "10px", marginTop: "10px" }}
              />
              <TextField
                label="Tehtävä"
                inputRef={uusiTehtavaRef}
                variant="outlined"
                fullWidth={true}
                sx={{ marginBottom: "10px" }}
              />
              <Button variant="contained" fullWidth={true} onClick={lisaaUusiHavainto}>
                Tallenna
              </Button>
              <Button fullWidth={true} component={Link} to="/">
                Peruuta
              </Button>
            </Paper>
          </Grid>
        </Grid>
      </Container>
    </LocalizationProvider>
  );
};

export default LisaaTehtava;