import React, { useEffect } from "react";
import {
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Typography,
} from "@mui/material";
import { format } from "date-fns";
import { Link } from "react-router-dom";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from '@mui/icons-material/Edit';

interface ListaProps {
  havainto: Havainto[];
}

const TehtavaLista: React.FC<ListaProps> = ({ havainto }): React.ReactElement => {
  useEffect(() => {
  }, [havainto]);

  const sortedHavainnot = [...havainto].sort(
    (a, b) => new Date(b.pvm).getTime() - new Date(a.pvm).getTime()
  );
  return (
    <List>
      <Typography variant="h6" sx={{ marginTop: "10px" }}>
        Tehtävät
      </Typography>

      {sortedHavainnot.map((havainnot: Havainto, index: number) => (
        <ListItem key={index}>
          <ListItemText
            primary={`Tehtävä: ${havainnot.laji}`}
            secondary={`Paikka: ${havainnot.paikka} - ${format(havainnot.pvm,"d.M.Y HH:mm")}`}
          ></ListItemText>
          <ListItemIcon>
            <IconButton component={Link} to={`/poistatehtava/${havainnot.id}`} edge="end">
              <DeleteIcon />
            </IconButton>
            <ListItemIcon>
              <IconButton component={Link} to={`/muokkaatehtava/${havainnot.id}`} edge='end'>
                <EditIcon />
              </IconButton>
            </ListItemIcon>
          </ListItemIcon>
        </ListItem>
      ))}
    </List>
  );
};

export default TehtavaLista;