import { useEffect, useState } from "react";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import cityData from "../data.json";
import { Button, CircularProgress, Container, FormControl, InputLabel, Link, MenuItem, Paper, Typography } from "@mui/material";

interface PaivanSaaProps {
    selectedCity: string;
    onSelectCity: (city: string) => void;
  }
  
  const PaivanSaa: React.FC<PaivanSaaProps> = ({
    selectedCity,
    onSelectCity,
  }): React.ReactElement => {
    const [data, setData] = useState<Data>({
      city: "",
      temperature: "",
      weather: {
        id: 0,
        main: "",
        description: "",
        icon: "",
      },
      virhe: "",
      valmis: false,
      cityNames: cityData.cityNames,
    });
  
    const [loading, setLoading] = useState(false);
  
    const handleCityChange = (event: SelectChangeEvent<string>): void => {
      const selectedCity = event.target.value;
      onSelectCity(selectedCity);
    };
  
    const normalizeCityName = (city: string): string => {
      return city
        .toLowerCase()
        .normalize("NFD")
        .replace(/[\u0300-\u036f]/g, "");
    };
  
    const fetchData = async (): Promise<void> => {
      try {
        setLoading(true);
  
        const response = await fetch(
          `https://xamkbit.azurewebsites.net/saatilanne/${normalizeCityName(
            selectedCity
          )}`
        );
        const weatherData = await response.json();
  
        if (weatherData.cod !== 200) {
          throw new Error("Kaupunkia ei löytynyt.");
        }
  
        setData({
          city: weatherData.name,
          temperature: weatherData.main?.temp?.toString() || "",
          weather: {
            id: weatherData.weather[0]?.id || 0,
            main: weatherData.weather[0]?.main || "",
            description: weatherData.weather[0]?.description || "",
            icon: weatherData.weather[0]?.icon || "",
          },
          virhe: "",
          valmis: true,
          cityNames: cityData.cityNames,
        });
      } catch (error: any) {
        setData({
          city: "",
          temperature: "",
          weather: {
            id: 0,
            main: "",
            description: "",
            icon: "",
          },
          virhe: error.message,
          valmis: true,
          cityNames: cityData.cityNames,
        });
      } finally {
        setLoading(false);
      }
    };
  
    useEffect(() => {
      if (selectedCity) {
        fetchData();
      }
    }, [selectedCity]);
  
    return (
      <Container sx={{ display: "flex" }}>
        <Paper
          sx={{ margin: 4, minWidth: "495px", minHeight: "100px", padding: 4 }}
        >
          <Typography variant="h6" style={{ marginBottom: "20px" }}>
            Päivän sää
          </Typography>
  
          <FormControl sx={{ minWidth: "200px", marginBottom: "20px" }}>
            <InputLabel id="city-selector-label">Valitse Kaupunki</InputLabel>
            <Select
              labelId="city-selector-label"
              id="city-selector"
              value={selectedCity}
              onChange={handleCityChange}
            >
              {data.cityNames.map((city: string) => (
                <MenuItem key={city} value={city}>
                  {city}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
  
          {loading ? (
            <CircularProgress />
          ) : data.valmis ? (
            <>
              <Typography>
                <b>Kaupunki</b>:<br /> {`${data.city}`}
              </Typography>
              <Typography>
                <b>Sää:</b> <br />
                {`${data.weather.description}`}
              </Typography>
              <Typography>
                <b>Lämpötila nyt:</b>
                <br /> {`${data.temperature} °C`}
              </Typography>
              {data.weather.icon && (
                <img
                  src={`http://openweathermap.org/img/wn/${data.weather.icon}.png`}
                  alt="Weather Icon"
                  style={{ width: "50px", height: "50px" }}
                />
              )}
              {/* <Button component={Link} to="/ennuste">
                Katso sää ennuste
              </Button> */}
            </>
          ) : (
            <Typography variant="body2">{data.virhe}</Typography>
          )}
        </Paper>
      </Container>
    );
  };
  
  export default PaivanSaa;