import React, { useEffect, useRef, useState } from "react";
import Header from "./components/Valikko";
import { Route, Routes } from "react-router-dom";
import Etusivu from "./components/Etusivu";
import PoistaTehtava from "./components/PoistaTehtava";
import MuokkaaTehtava from "./components/MuokkaaTehtava";
import LisaaTehtava from "./components/LisaaTehtava";

const App: React.FC = (): React.ReactElement => {
  const [havainto, setHavainto] = useState<Havainto[]>([]);
  const tarkistusRef: React.MutableRefObject<boolean> = useRef(false);

  useEffect(() => {
    // console.log("App komponissa");
    if (!tarkistusRef.current) {
      // console.log("(App.tsx)Katotaan localStorageen(App.tsx)...");
      const localStorageData = localStorage.getItem("havaintolista");

      if (localStorageData) {
        console.log("(App.tsx)Dataa localStrogaessa (App.tsx):", localStorageData);

        setHavainto(
          JSON.parse(localStorageData).map((havainto: Havainto) => ({
            ...havainto,
            pvm: new Date(havainto.pvm),
          }))
        );
        console.log("(App.tsx)Data haettu localStoragesta(App.tsx):", havainto);
        console.log("(App.tsx)Havainto state päivititettu (App.tsx):", havainto);
      } else {
        console.log("(App.tsx)Dataa ei löytynyt LocalStrogaesa");
      }
    }



    return () => {
      tarkistusRef.current = true;
      console.log("(App.tsx) Tyhjennys käynnistyi(App.tsx)");
    };
  }, []);

  useEffect(() => {
    localStorage.setItem("havaintolista", JSON.stringify(havainto));
    console.log("(App.tsx) Data tallennettu localStoragen(App.tsx)", havainto);
  }, [havainto]);

  return (

      <>
        <Header>Lintubongaus</Header>
        <Routes>
          <Route path="/" element={<Etusivu havainto={havainto} />} />
          <Route path="/poistatehtava/:id" element={<PoistaTehtava havainto={havainto} setHavainto={setHavainto} />} />
          <Route path="/muokkaatehtava/:id" element={<MuokkaaTehtava havainto={havainto} setHavainto={setHavainto} />} />
          <Route path="lisaatehtava" element={<LisaaTehtava havainto={havainto} setHavainto={setHavainto} />}/>
        </Routes>
      </>
    )}



export default App;